import "core-js/modules/es.array.push.js";
import bottomNav from "@/components/bottomNav.vue";
import { reactive, ref, toRefs } from "@vue/reactivity";
import { getBookData, getBookShopData } from "../assets/api/home";
import { useRouter } from "vue-router";
import { Toast } from "vant";
import store from "@/store";
import "vant/es/toast/style";
export default {
  components: {
    bottomNav
  },
  setup() {
    //判断购物车是否有内容
    let carnum = ref(0);
    let carData = () => {
      carnum.value = store.state.shoppingCar.length;
      // console.log(carnum.value);
    };
    // 下拉刷新
    const loading = ref(false);
    const onRefresh = () => {
      setTimeout(() => {
        getBookShopData().then(data => {
          bannerDataFun.bookData = data.data.results.list;
          // console.log(bannerDataFun.bookData);
          loading.value = false;
          Toast("刷新成功");
        });
      }, 1000);
    };

    //获取数据
    let bannerDataFun = reactive({
      bannerData: [],
      bookData: [],
      Index: 0
    });
    //轮播图数据
    let getBookDataFun = () => {
      getBookData().then(data => {
        // console.log(data);
        //获取轮播图的图片
        bannerDataFun.bannerData = data.data.results.list;
      });
    };
    //书本书籍数据
    let getBookShopDataFun = () => {
      getBookShopData().then(data => {
        bannerDataFun.bookData = data.data.results.list;
        // console.log(bannerDataFun.bookData);

        console.log(bannerDataFun.bookData);
      });
    };
    getBookDataFun();
    getBookShopDataFun();
    let router = useRouter();
    // 跳转到搜索页
    let goserach = () => {
      router.push({
        path: "/serach"
      });
    };

    // 跳转购物车
    let goCar = () => {
      router.push({
        path: "/shoppingcar"
      });
    };
    //跳转详情
    let goShop = id => {
      // console.log(id);
      router.push({
        path: "/shopping",
        query: {
          key: id
        }
      });
    };
    let totag = ref(0);
    let toprice = ref(0);
    let low = ref(true);
    //销量
    let xiaoliang = () => {
      bannerDataFun.Index = 1;
      let tem = null;

      //单次点击
      if (totag.value % 2 != 0) {
        //排序
        for (var i = bannerDataFun.bookData.length; i > 0; i--) {
          for (var j = 0; j < i - 1; j++) {
            if (parseInt(bannerDataFun.bookData[j].saleNum) > parseInt(bannerDataFun.bookData[j + 1].saleNum)) {
              tem = bannerDataFun.bookData[j];
              bannerDataFun.bookData[j] = bannerDataFun.bookData[j + 1];
              bannerDataFun.bookData[j + 1] = tem;
            }
          }
        }

        // console.log(totag.value);
      } else {
        bannerDataFun.bookData = bannerDataFun.bookData.reverse();
        //  console.log(totag.value);
      }

      totag.value += 1;
    };

    //价格
    let price = () => {
      bannerDataFun.Index = 2;
      let tem = null;
      if (toprice.value % 2 != 0) {
        for (var i = bannerDataFun.bookData.length; i > 0; i--) {
          for (var j = 0; j < i - 1; j++) {
            if (parseFloat(bannerDataFun.bookData[j].disprice) > parseFloat(bannerDataFun.bookData[j + 1].disprice)) {
              tem = bannerDataFun.bookData[j];
              bannerDataFun.bookData[j] = bannerDataFun.bookData[j + 1];
              bannerDataFun.bookData[j + 1] = tem;
            }
          }
        }
      } else {
        bannerDataFun.bookData = bannerDataFun.bookData.reverse();
      }
      toprice.value += 1;
      low.value = !low.value;
    };
    let zonghe = () => {
      bannerDataFun.Index = 0;
    };
    const show = ref(false);
    const showPopup = () => {
      show.value = true;
    };
    carData();
    return {
      ...toRefs(bannerDataFun),
      goShop,
      goserach,
      goCar,
      xiaoliang,
      price,
      zonghe,
      loading,
      onRefresh,
      carData,
      carnum,
      low,
      showPopup,
      show
    };
  }
};